<template>
  <section>
    <div class="search_bg">
      <div class="container">
        <input
          class="search_input p-2 m-2"
          type="text"
          placeholder="搜尋：輸入完畢後 Enter"
          @keyup.enter="submitSearch"
        >
        <button class="search_clear p-2" @click="clearSearch">清除關鍵字</button>
      </div>
    </div>
    <div class="clear"></div>
    <div class="container">
      <div class="search_type">
        <ul>
          <p
            class="filter__box p-1 m-1"
            v-for="(site, index, checkSite_key) in checkSite"
            :key="checkSite_key"
            @click="clearSite(index)"
          >
            {{site}}
            <font-awesome-icon icon="times"/>
          </p>
          <p
            class="filter__box p-1 m-1"
            v-for="(town, index, checkTown_key) in checkTown"
            :key="checkTown_key"
            @click="clearTown(index)"
          >
            {{town}}
            <font-awesome-icon icon="times"/>
          </p>
          <p
            class="filter__box p-1 m-1"
            v-for="(word, index, searchWords_key) in searchWords"
            :key="searchWords_key"
            @click="clearWord(index)"
          >
            {{word}}
            <font-awesome-icon icon="times"/>
          </p>
        </ul>
        <hr>
        <ul>地點
          <li class="filter__item" v-for="(site, index, site_key) in filterSite" :key="site_key">
            <input
              class="filter__input"
              type="checkbox"
              :value="site"
              :id="site"
              v-model="checkSite"
            >
            <label :for="site" @click="pageTurn">{{site}}</label>
          </li>
        </ul>
        <ul>區域
          <li class="filter__item" v-for="(town, index, town_key) in filterTown" :key="town_key">
            <input
              class="filter__input"
              type="checkbox"
              :value="town"
              :id="town"
              v-model="checkTown"
            >
            <label :for="town" @click="pageTurn">{{town}}</label>
          </li>
        </ul>
      </div>
      <div class="row justify-content-center">
        <div
          class="col-lg-4 col-sm-12 mt-3"
          v-for="(data, index, data_key) in filterData.slice(pageStart, pageStart + countOfPage)"
          :key="data_key"
        >
          <div class="main_card">
            <div
              class="main_card_top"
              :style="{ 'background-image': 'url(' + data.imageUrl + ')' }"
            ></div>
            <div class="main_card_bottom">
              <div class="container pt-3 pb-3">
                <div class="main_card_time">
                  <p class="m-0">
                    <font-awesome-icon icon="clock"/>
                    {{data.startDate}}～{{data.endDate}}
                  </p>
                </div>
                <div class="main_card_title">
                  <router-link
                    :to="{ name: 'paramspage', params: { title: data.title }}"
                  >{{index+1}}. {{data.title}}</router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-12 search_pagination p1 text-center">
        <ul>
          <li
            class="page-item"
            v-bind:class="{'disabled': (currPage === 1)}"
            @click.prevent="setPage(currPage-1)"
          >
            <a class="page-link" href>Prev</a>
          </li>
          <li
            class="page-item"
            v-for="n in totalPage"
            v-bind:class="{'active': (currPage === (n))}"
            @click.prevent="setPage(n)"
            :key="n"
          >
            <a class="page-link" href>{{n}}</a>
          </li>
          <li
            class="page-item"
            v-bind:class="{'disabled': (currPage === totalPage)}"
            @click.prevent="setPage(currPage+1)"
          >
            <a class="page-link" href>Next</a>
          </li>
        </ul>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  name: "search",
  data() {
    return {
      checkSite: [],
      checkTown: [],
      searchWords: [],
      // 分頁
      countOfPage: 9,
      currPage: 1
    };
  },
  props: {
    showData: {
      type: Array,
      required: true
    },
    siteData: {
      type: Array,
      required: true
    },
    place: {
      type: Object,
      required: true
    }
  },
  computed: {
    filterSite() {
      let SearchArrSite = [];
      this.siteData.filter(el => {
        return SearchArrSite.push(el.name);
      });
      return SearchArrSite.filter((el, i, arr) => {
        return arr.indexOf(el) === i;
      });
    },
    filterTown() {
      let SearchArrTown = [];
      this.siteData.forEach(el => {
        if (this.checkSite.indexOf(el.name) > -1 ? true : false) {
          el.towns.filter(el => {
            return SearchArrTown.push(el.name);
          });
        }
      });
      return SearchArrTown.filter((el, i, arr) => {
        return arr.indexOf(el) === i;
      });
    },
    allkeywords() {
      return [].concat(this.checkSite, this.checkTown, this.searchWords);
    },
    // 資料
    filterData() {
      // 關鍵字
      let keyword = this.allkeywords.filter(el => {
        return el.toLowerCase();
      });
      let RegExpKeyword = new RegExp(keyword.join("|"), "g");
      
      let filterAll = this.showData.filter(el => {
        return el.imageUrl != "";
      });

      let taipei = /台北|臺北/g;
      let taichung = /台中|臺中/g;
      let tainan = /台南|臺南/g;

      return keyword !== ""
        ? filterAll.filter(function(d) {
            if (keyword == "台北") {
              return d.showInfo[0].location.search(taipei) != -1;
            } else if (keyword == "台中") {
              return d.showInfo[0].location.search(taichung) != -1;
            } else if (keyword == "台南") {
              return d.showInfo[0].location.search(tainan) != -1;
            } else {
              return (
                d.title.toLowerCase().search(RegExpKeyword) > -1 ||
                d.showInfo[0].location.toLowerCase().search(RegExpKeyword) > -1
              );
            }
          })
        : filterAll;
    },
    // 分頁
    pageStart() {
      return (this.currPage - 1) * this.countOfPage;
    },
    totalPage() {
      return Math.ceil(this.filterData.length / this.countOfPage);
    }
  },
  methods: {
    clearSearch() {
      this.checkSite = [];
      this.checkTown = [];
      this.searchWords = [];
    },
    clearSite(index) {
      this.checkSite.splice(index, 1);
    },
    clearTown(index) {
      this.checkTown.splice(index, 1);
    },
    clearWord(index) {
      this.searchWords.splice(index, 1);
    },
    submitSearch(event) {
      this.searchWords.push(event.target.value);
      event.target.value = "";
      this.currPage = 1;
    },
    // 分頁
    setPage: function(idx) {
      if (idx <= 0 || idx > this.totalPage) {
        return;
      }
      this.currPage = idx;
    },
    pageTurn() {
      this.currPage = 1;
    }
  }
};
</script>
<style lang="scss">
.filter__box {
  font-size: 16px;
  display: inline;
  transition: 0.5s;
  cursor: pointer;
  &:hover {
    border-radius: $rounded-pill;
    background-color: $danger;
    color: #fff;
  }
}
.main_card_top {
  background-size: contain;
  background-position: 50%;
  background-repeat: no-repeat;
}
</style>
